/** Get the restaurant block levels in a time window, 15 min each */
export function listBusyBlocks(payload: object): RequestOptionsFactory<any> {
  function __listBusyBlocks({ host }: any) {
    const serializedData = transformPaths(payload, [
      {
        transformFn: transformSDKTimestampToRESTTimestamp,
        paths: [{ path: 'from' }, { path: 'until' }],
      },
    ]);
    const metadata = {
      entityFqdn: 'wix.restaurants.order_pacing.v1.order',
      method: 'GET' as any,
      methodFqn:
        'com.wixpress.restaurants.order.pacing.OrderPacingService.ListBusyBlocks',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveComWixpressRestaurantsOrderPacingOrderPacingServiceUrl({
        protoPath: '/v1/busy-blocks',
        data: serializedData,
        host,
      }),
      params: toURLSearchParams(serializedData),
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'busyBlocks.startTime' },
              { path: 'busyBlocks.endTime' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __listBusyBlocks;
}
